import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { Observable } from "rxjs";
import { tap } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class ReportService {
  private baseUrl = environment.apiUrl;
  private path = "report";
  constructor(private _http: HttpClient) {}

  customerInvoiceFabricReport(invoiceId): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/customerInvoice`, invoiceId)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  goodsReceivingNoteReport(invoiceId): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/goodReceivingNote`, invoiceId)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  orderInvoiceReport(orderId): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/invoiceReport`, orderId)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  customerLedgerReport(customerData): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/ledgerReport`, customerData)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  expensesDetailReport(expenseDetail): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/expenseDetail`, expenseDetail)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  expensesSummaryReport(expenseSummary): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/expenseSummary`, expenseSummary)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  orderSummaryReport(orderSummary): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/orderSummary`, orderSummary)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  invoiceSummaryReport(orderSummary): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/invoiceSummary`, orderSummary)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  inventoryActivityReport(orderSummary): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/inventoryActivity`, orderSummary)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  inventoryReport(): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/inventoryReport`,{})
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  karigarSummaryReport(karigarSummary): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/karigarSummary`, karigarSummary)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  karigarLedgerReport(karigarLedger): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/karigarLedger`, karigarLedger)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  wholeSalePurchaseReport(dateRange): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/wholeSalePurchase`, dateRange)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  wholeSaleSaleReport(dateRange): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/wholeSaleSale`, dateRange)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }

  stichCustomerOrderReport(dateRange): Observable<any> {
    return this._http
      .post<any>(`${this.baseUrl}/${this.path}/stichCustomerOrder`, dateRange)
      .pipe(
        tap((res) => {
          return res;
        })
      );
  }
}
