export function dateFormatter(date) {
  // Create a new Date object from the provided date string
  const formattedDate = new Date(date);
  const day = formattedDate.getDate().toString().padStart(2, "0");
  const month = (formattedDate.getMonth() + 1).toString().padStart(2, "0"); // Note: Month is zero-based
  const year = formattedDate.getFullYear();

  // Return the formatted date
  return `${day}-${month}-${year}`;
}

export function thousandSeparator(number) {
  // Convert the number to a string
  let numberString = number.toString();

  // Add thousand separators
  const regex = /\B(?=(\d{3})+(?!\d))/g;
  numberString = numberString.replace(regex, ",");

  return numberString;
}

export function handleNullUndefinedValues(obj) {
  // If the input is an object
  if (typeof obj === "object" && obj !== null) {
    // If the input is an array
    if (Array.isArray(obj)) {
      // Iterate over array elements
      return obj.map((element) => handleNullUndefinedValues(element));
    } else {
      // Iterate over object keys
      for (let key in obj) {
        // Check if the value is null or undefined
        if (obj[key] === null || obj[key] === undefined) {
          // Replace null or undefined with empty string
          obj[key] = "";
        } else if (typeof obj[key] === "object") {
          // Recursively handle nested objects or arrays
          obj[key] = handleNullUndefinedValues(obj[key]);
        }
      }
      return obj;
    }
  } else {
    return obj;
  }
}
